
import { Container, Row, Col, Tab, Nav } from "react-bootstrap";
import { ProjectCard } from "./ProjectCard";
import projImg1 from "../assets/img/project-img1.png";
import projImg2 from "../assets/img/project-img2.jpg";
import projImg3 from "../assets/img/project-img3.png";
import colorSharp2 from "../assets/img/color-sharp2.png";
import 'animate.css';
import TrackVisibility from 'react-on-screen';

export const Projects = () => {

  const projects = [
    {
      title: "Text & Word Analyzer App",
      description: "ReactJs",
      imgUrl: projImg1,
      link: 'https://google.com' // Add your link here
    },
    {
      title: "News WebApp",
      description: " ReactJs + APIs",
      imgUrl: projImg2,
    },
    {
      title: "myNotebook",
      description: "Reactjs + MongoDb",
      imgUrl: projImg3,
    },
    
  ];

  return (
    <section className="project" id="projects">
      <Container>
        <Row>
          <Col size={12}>
            <TrackVisibility>
              {({ isVisible }) =>
              <div className={isVisible ? "animate__animated animate__fadeIn": ""}>
                <h2>Projects</h2>
                <p style={{ textAlign: 'justify',marginBottom:'50px' }}> I have worked on various projects such as <a href="#" target="_blank" rel="noopener noreferrer">Text Analyzer Web App</a>, <a href="#" target="_blank" rel="noopener noreferrer">NewsWebApp</a>, <a href="#" target="_blank" rel="noopener noreferrer">myNoteBook</a> that demonstrate my skills in web development, problem-solving, and software design. Each project reflects my commitment to building efficient, user-friendly applications and my ability to adapt to different technologies and frameworks. Get projects source code from <a href="https://github.com/AhmedKhalid013" target="_blank" rel="noopener noreferrer"> here.</a></p>



                <Tab.Container id="projects-tabs" defaultActiveKey="first">
                

               {/* <p style={{ color:'red'}}> See Project Sites Cilck 2.</p>
                <p style={{ color: 'red'}}> See Project Sites Cilck 3.</p>*/}
                
                  {/*
                  <Nav variant="pills" className="nav-pills mb-5 justify-content-center align-items-center" id="pills-tab">
                    <Nav.Item>
                      <Nav.Link eventKey="first">Tab 1</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                      <Nav.Link eventKey="second">Tab 2</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                      <Nav.Link eventKey="third">Tab 3</Nav.Link>
                    </Nav.Item>
                  </Nav>*/}        
                  <Tab.Content id="slideInUp" className={isVisible ? "animate__animated animate__slideInUp" : ""}>
                    <Tab.Pane eventKey="first">
                      <Row>
                        {
                          projects.map((project, index) => {
                            return (
                              <ProjectCard
                                key={index}
                                {...project}
                                
                                />
                            )
                          })
                        }
                      </Row>

                    </Tab.Pane>
                    <Tab.Pane eventKey="section">
                      <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Cumque quam, quod neque provident velit, rem explicabo excepturi id illo molestiae blanditiis, eligendi dicta officiis asperiores delectus quasi inventore debitis quo.</p>
                    </Tab.Pane>
                    <Tab.Pane eventKey="third">
                      <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Cumque quam, quod neque provident velit, rem explicabo excepturi id illo molestiae blanditiis, eligendi dicta officiis asperiores delectus quasi inventore debitis quo.</p>
                    </Tab.Pane>
                  </Tab.Content>
                </Tab.Container>
              </div>}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
      <img className="background-image-right" src={colorSharp2}></img>
    </section>
  )
}

